import React from "react";
import { graphql } from "gatsby";
import { GatsbyImage } from "gatsby-plugin-image";
import { Header, Icon, Button, Grid } from "semantic-ui-react";

import { Helmet } from "react-helmet";

import SEO from "../components/seo";

export default function MisconceptionIFrameTemplate({ pageContext, data }) {
  const { mdx } = data;
  const { slug } = pageContext;

  return (
    <>
      <Helmet async={false}>
        {/* <!-- You MUST include jQuery before Fomantic --> */}
        <script src='https://cdn.jsdelivr.net/npm/jquery@3.3.1/dist/jquery.min.js'></script>
        <link
          rel='stylesheet'
          type='text/css'
          href='https://cdn.jsdelivr.net/npm/fomantic-ui@2.8.7/dist/semantic.min.css'
        />
        <script src='https://cdn.jsdelivr.net/npm/fomantic-ui@2.8.7/dist/semantic.min.js'></script>

        <style id='iframe-style' type='text/css'>{`
          body {
            background-color: #faefdb;
          }
          div#misconception-body > p > code.language-text {
            background-color: #ffffff40;
            color: white;
          }
        `}</style>
      </Helmet>
      <SEO
        title={mdx.fields.name}
        cardTitle={`Misconception: ${mdx.fields.name}`}
        imageRelativeUrl={
          mdx.fields.image ? mdx.fields.image.childImageSharp.resize.src : null
        }
        description={mdx.frontmatter.shortDescription}
        useLargeImageCard={true}
      />
      <div
        style={{
          height: "100%",
          marginTop: "0px",
          marginBottom: "0px",
          background: "#faefdb",
        }}
      >
        <div style={{ padding: "1em" }}>
          <Grid className='relaxed'>
            <Grid.Row style={{ padding: "0.5rem 0 0 0" }}>
              <Grid.Column width={8}>
                <div className='ui inverted raised red segment'>
                  <Header as='h1' inverted>
                    <Header.Content>{mdx.fields.name}</Header.Content>
                  </Header>
                </div>
              </Grid.Column>
              <Grid.Column width={8}>
                <Button
                  floated='right'
                  color='brown'
                  as='a'
                  href={slug}
                  target='_blank'
                >
                  Learn More
                  <Icon name='right arrow' />
                </Button>
              </Grid.Column>
            </Grid.Row>
            <Grid.Row>
              <Grid.Column width={8}>
                <div
                  className='ui inverted raised red segment'
                  style={{ height: "100%" }}
                >
                  <div className='ui header'>Incorrect</div>
                  <p style={{ textWrap: "pretty" }}>
                    {mdx.frontmatter.shortDescription}
                  </p>
                </div>
              </Grid.Column>
              <Grid.Column width={8}>
                <div
                  className='ui inverted raised green segment'
                  style={{ height: "100%" }}
                >
                  <div className='ui header'>Correct</div>
                  <p style={{ textWrap: "pretty" }}>
                    {mdx.frontmatter.shortCorrection}
                  </p>
                </div>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </div>
        {mdx.fields.image ? (
          <GatsbyImage
            image={mdx.fields.image.childImageSharp.gatsbyImageData}
            className='ui image'
            alt={mdx.fields.name}
            style={{ width: "10242px" }}
          />
        ) : null}
      </div>
    </>
  );
}

export const pageQuery = graphql`
  query ($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      body
      rawBody
      fields {
        name
        pl
        image {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH)
            resize(width: 1024, height: 512, fit: COVER, cropFocus: NORTH) {
              src
              height
            }
          }
        }
      }
      frontmatter {
        shortDescription
        shortCorrection
        status
        concepts
      }
    }
  }
`;
